class DownloadCertificatesInBulk {
	constructor(){

		try {
			this.getElements();
			this.getCleanUpMethod();
			this.getClearMethod();
		}
		catch ( e ){ console.log( e ); }

	}

	getElements(){
		this.$elements = {
			filters: {
				uo_cert_cleanup: $('.uo_certificates_cleanup_btn'),
				uo_cert_success: $('#uo-download-cert-in-bulk-success'),
				uo_cert_warning: $('#uo-download-cert-in-bulk-warning'),
				uo_cert_clear: $('.uo_certificates_clear_btn'),
				uo_cert_clear_success : $('#uo-cert-in-bulk-clear-success'),
				uo_cert_clear_warning : $('#uo-cert-in-bulk-clear-warning'),
			}
		};
	}

	getCleanUpMethod(){
		this.$elements.filters.uo_cert_cleanup.on( 'click', ( e ) => {
			console.log('Certificates are cleaned');
			const data = new FormData();
			fetch(UncannyToolkitPro.restURL+'uo-cleanup-certificates', {
				method: "POST",
				body: data
			})
			.then((response) => response.json())
			.then((data) => {
				if( data.success === true ){
					this.$elements.filters.uo_cert_success.css('display','block');
				} else {
					this.$elements.filters.uo_cert_warning.css('display','block');
				}
			})
			.catch((error) => {
				console.error(error);
			});
		});
	}

	getClearMethod(){
		this.$elements.filters.uo_cert_clear.on( 'click', ( e ) => {
			const data = new FormData();
			fetch(UncannyToolkitPro.restURL+'uo-clear-certificates-previous-runs', {
				method: "POST",
				body: data
			})
			.then((response) => response.json())
			.then((data) => {
				if( data.success === true ){
					this.$elements.filters.uo_cert_clear_success.css('display','block');
				} else {
					this.$elements.filters.uo_cert_clear_warning.css('display','block');
				}
			})
			.catch((error) => {
				console.error(error);
			});
		});
	}


}

export default DownloadCertificatesInBulk;
