import {restCall} from "../../common/js/utilities";

class LdDripNotification {
    constructor() {

        if (this.shouldLoad()) {
            // Listen to clicks on the buttons
            this.listenActions();
        }
    }

    shouldLoad() {
        if (typeof UO_LD_Notifications_String !== 'undefined' && UO_LD_Notifications_String.length > 0) {
            return true;
        }
        return false;
    }

    listenActions() {

        $(document).ready(function () {
            if (("_ld_notifications_group_id" in UO_LD_Notifications_String)) {
                let $group_id = UO_LD_Notifications_String._ld_notifications_group_id;
                if (0 !== $group_id) {
                    $('select[name="_ld_notifications_group_id"] option[value=' + $group_id + ']').prop('selected', 'selected');
                }
            }
        });

        // Listed Trigger
        $('select[name="_ld_notifications_trigger"]').change(function () {

            // Prevent default
            event.preventDefault();

            let $trigger = $(this).val();
            if ('group_lesson_available' !== $trigger) {
                return;
            }

            $('select[name="_ld_notifications_course_id"]').html(
                '<option value="all">' + UO_LD_Notifications_String.select_group_first + '</option>'
            );
        });

        // Listen group ID change select
        $('select[name="_ld_notifications_group_id"]').change(function () {

            // Prevent default
            event.preventDefault();

            let $group_id = $(this).val();
            let $trigger = $('select[name="_ld_notifications_trigger"]').val();
            if ('group_lesson_available' !== $trigger) {
                return;
            }

            const data = {
                ...{
                    group_id: $group_id,
                },
            }

            restCall('get_group_courses', data, (response) => {
                response = response.data;

                $('select[name="_ld_notifications_course_id"]').html(
                    '<option value="">' + UO_LD_Notifications_String.select_course + '</option>' +
                    '<option value="all">' + UO_LD_Notifications_String.all_courses + '</option>'
                );

                $.each(response, function (i, val) {
                    $('select[name="_ld_notifications_course_id"]').append('<option value="' + i + '">' + val + '</option>');
                });
            });
        });
    }
}

export default LdDripNotification;