import {
	restCall,
	isEmpty,
	isDefined
} from '../../common/js/utilities.js';

class TryAutomator {
	constructor(){
		// Get the elements
		this.getElements();

		// Check if the element exists
		if ( this.$elements.tryAutomatorXIcon.length == 1 ){
			// Listen clicks to it
			this.handleHide();
		}
	}

	getElements(){
		this.$elements = {
			tryAutomatorXIcon: $( '#ultp-sidebar-try-automator-close' )
		}
	}

	handleHide(){
		// Listen clicks to the "X" icon
		this.$elements.tryAutomatorXIcon.on( 'click', ( event ) => {
			// Prevent default
			event.preventDefault();

			// Get the row
			const $itemContainer = $( event.currentTarget ).closest( '.ult-sidebar-featured-item-container' );

			// Add loading class to the row
			$itemContainer.addClass( 'ult-sidebar-featured-item-container--loading' );

			// Do a call to hide the row
			restCall( 'try-automator-visibility', {
				action: 'hide-forever'
			}, ( response ) => {
				// Check if the task was successful
				if ( response.success ){
					// Hide the row
					$itemContainer.remove();
				}
				else {
					// Remove the loading class to the row
					$itemContainer.removeClass( 'ult-sidebar-featured-item-container--loading' );
				}
			}, () => {
				// Remove the loading class to the row
				$itemContainer.removeClass( 'ult-sidebar-featured-item-container--loading' );
			});
		});
	}
}

export default TryAutomator;