const TomSelect = require( 'tom-select/dist/js/tom-select.complete.js' );

class RestrictPageAccess {
	constructor(){
		// Get elements
		this.getElements();

		// Add select2 to dropdowns
		this.initSelect2();

		// Listen changes to the "Enable restrict access" checkbox
		this.listenChangesToEnableCheckbox();

		// Listen changes to the "Redirect" checkbox
		this.listenChangesToRedirectCheckbox();

		// Listen changes to the "Redirect to" radios
		this.listenChangesToRedirectTo();
	}

	getElements(){
		// Get elements
		this.$elements = {
			metabox:                   $( '#ultp-restrict-access' ),
			restrictAccessCheckbox:    $( '#ultp-restrict-access__enable-restrict-access' ),
			redirectIfDefinedCheckbox: $( '#ultp-restrict-access__redirect-if-defined' ),
			redirectToRadio:           $( '.ultp-restrict-access__redirect-to' ),
			selects:                   $( '.ultp-restrict-access__option select' ),
		}
	}

	initSelect2(){
		// Add select2 pillboxes to multi selects
		// this.$elements.selects.select2();

		$.each( this.$elements.selects, ( index, $select ) => {
			new TomSelect( $select, {
                plugins: [ 'no_active_items', 'no_backspace_delete', 'remove_button' ],
            } );
		} );
	}

	listenChangesToEnableCheckbox(){
		// Listen if the checkbox changes
		this.$elements.restrictAccessCheckbox.on( 'change', ( event ) => {
			// Check if it's checked
			if ( this.$elements.restrictAccessCheckbox.is( ':checked' ) ){
				// Add "enabled" class
				this.$elements.metabox.addClass( 'ultp-restrict-access--enabled' );
			}
			else {
				// Remove "enabled" class
				this.$elements.metabox.removeClass( 'ultp-restrict-access--enabled' );
			}
		});
	}

	listenChangesToRedirectCheckbox(){
		// Listen if the checkbox changes
		this.$elements.redirectIfDefinedCheckbox.on( 'change', ( event ) => {
			// Check if it's checked
			if ( this.$elements.redirectIfDefinedCheckbox.is( ':checked' ) ){
				// Add "redirect-if-denied" class
				this.$elements.metabox.addClass( 'ultp-restrict-access--redirect-if-denied' );
			}
			else {
				// Remove "redirect-if-denied" class
				this.$elements.metabox.removeClass( 'ultp-restrict-access--redirect-if-denied' );
			}
		});
	}

	listenChangesToRedirectTo(){
		// Listen if the checkbox changes
		this.$elements.redirectToRadio.on( 'change', ( event ) => {
			// Get the selected value
			const selectedValue = this.$elements.redirectToRadio.filter( ':checked' ).val();

			// Remove all the classes
			this.$elements.metabox.removeClass( 'ultp-restrict-access--redirect-to-post ultp-restrict-access--redirect-to-custom_url' );

			// Add class to the metabox
			this.$elements.metabox.addClass( `ultp-restrict-access--redirect-to-${ selectedValue }` );
		});
	}
}

export default RestrictPageAccess;
