import {
	restCall,
	isEmpty,
	isDefined
} from '../../common/js/utilities.js';

class GroupDrip {
	constructor(){
		// Get elements
		this.getElements();

		// Check if it has the group drip table
		if ( this.hasGroupDripTable() ){
			// Init DataTables in the table
			this.addDataTables();

			// Listen to clicks on the buttons
			this.listenActions();
		}
	}

	getElements(){
		// Get elements
		this.$elements = {
			table: $( '#uo-ld-group-drip' )
		}
	}

	listenActions(){

		// Listen clicks to the save date button
		this.$elements.table.on( 'click', '.uo-ld-group-drip__action--save', ( event ) => {
			// Prevent default
			event.preventDefault();

			// Get button
			const $saveButton = $( event.currentTarget );

			// Get row
			const $row = $saveButton.closest( 'tr' );

			// Remove the error class from all the fields
			// This is useful if the fields were invalid
			// but are valid now
			$row.find( 'input, select' ).removeClass( 'ultp-gutenberg-field--invalid' );

			// Get fields
			const $fields = {
				year:      $row.find( 'input[name="year"]' ),
				month:     $row.find( 'select[name="month"]' ),
				day:       $row.find( 'input[name="day"]' ),
				hour:      $row.find( 'input[name="hour"]' ),
				minute:    $row.find( 'input[name="minute"]' ),
			}

			// Get fields values
			const fieldsValue = {
				year:      $fields.year.val(),
				month:     $fields.month.val(),
				day:       $fields.day.val(),
				hour:      $fields.hour.val(),
				minute:    $fields.minute.val(),
			}

			// Get data. Merge the fields value
			const data = {
				...{
					group_id:  $row.data( 'group' ),
					post_id:   $row.data( 'post' ),
					course_id: $row.data( 'course' ),

					action:    'save',
				},
				...fieldsValue
			}

			// Validate fields. Create the variable to save the
			// result of the validation and set true as the default
			// value
			let isValid = true;

			// Check if the fields are numeric and aren't empty
			for ( let fieldId in fieldsValue ){
				// Check if it's empty or it's not a number
				if ( isEmpty( fieldsValue[ fieldId ] ) || isNaN( parseInt( fieldsValue[ fieldId ] ) ) ){
					// Update validation result
					isValid = false;

					// Add error class to the field
					$fields[ fieldId ].addClass( 'ultp-gutenberg-field--invalid' );
				}

				let fieldValue = parseInt( $fields[ fieldId ][0].value );

				switch(fieldId) {
					case 'year':
						if (fieldValue < 2015 || fieldValue > 2100) {
							// Update validation result
							isValid = false;

							// Add error class to the field
							$fields[ fieldId ].addClass( 'ultp-gutenberg-field--invalid' );
						}
						break;
					case 'month':
						if (fieldValue === 0 || fieldValue > 12) {
							// Update validation result
							isValid = false;

							// Add error class to the field
							$fields[ fieldId ].addClass( 'ultp-gutenberg-field--invalid' );
						}
						break;
					case 'day':
						if (fieldValue === 0 || fieldValue > 31) {
							// Update validation result
							isValid = false;

							// Add error class to the field
							$fields[ fieldId ].addClass( 'ultp-gutenberg-field--invalid' );
						}
						break;
					case 'hour':
						if ( fieldValue > 23) {
							// Update validation result
							isValid = false;

							// Add error class to the field
							$fields[ fieldId ].addClass( 'ultp-gutenberg-field--invalid' );
						}
						break;
					case 'minute':
						if ( fieldValue > 59) {
							// Update validation result
							isValid = false;

							// Add error class to the field
							$fields[ fieldId ].addClass( 'ultp-gutenberg-field--invalid' );
						}
						break;
				}
			}

			// Check if it's valid
			if ( isValid ){
				// Add loading class to the button and to the row
				$row.addClass( 'uo-ld-group-drip-row--loading' );
				$saveButton.addClass( 'ultp-gutenberg-btn--loading' );

				// Do REST call
				restCall( 'update_drip_date', data, ( response ) => {
					// Remove loading class from the button and to the row
					$row.removeClass( 'uo-ld-group-drip-row--loading' );
					$saveButton.removeClass( 'ultp-gutenberg-btn--loading' );

					// On success
					if ( response.success ){
						// Add success class
						$row.addClass( 'uo-ld-group-drip-row--success' );
						console.log(data);
						let date = data.year + '-' + data.month + '-' + data.day + ' ' + data.hour + ':' + data.minute + ':00';

						let timestamp = (new Date(date)).getTime();
						$row.data('sort',timestamp);

						this.$elements.dataTable.draw(false);
					}
					else {
						// Add error class
						$row.addClass( 'uo-ld-group-drip-row--error' );
						alert( 'Failed to save: ' + response.data.message );
					}

					// Remove class after three seconds
					setTimeout(() => {
						// Remove success and error classes
						$row.removeClass( 'uo-ld-group-drip-row--success uo-ld-group-drip-row--error' );
						location.reload();
					}, 2000 );
				}, ( response ) => {
					// Add error class
					$row.addClass( 'uo-ld-group-drip-row--error' );

					// Remove class after three seconds
					setTimeout(() => {
						// Remove success and error classes
						$row.removeClass( 'uo-ld-group-drip-row--success uo-ld-group-drip-row--error' );
						location.reload();
					}, 2000 );
				});
			}
		});

		// Listen clicks to the remove date button
		this.$elements.dataTable.on( 'click', '.uo-ld-group-drip__action--remove', ( event ) => {
			// Prevent default
			event.preventDefault();

			// Get button
			const $removeButton = $( event.currentTarget );

			// Get row
			const $row = $removeButton.closest( 'tr' );

			// Get data
			const data = {
				group_id: $row.data( 'group' ),
				post_id:  $row.data( 'post' ),
				course_id:$row.data( 'course' ),

				action:   'remove'
			}

			// Add loading class to the button and to the row
			$row.addClass( 'uo-ld-group-drip-row--loading' );
			$removeButton.addClass( 'ultp-gutenberg-btn--loading' );

			// Do REST call
			restCall( 'update_drip_date', data, ( response ) => {
				// Remove loading class from the button and to the row
				$row.removeClass( 'uo-ld-group-drip-row--loading' );
				$removeButton.removeClass( 'ultp-gutenberg-btn--loading' );

				// On success
				if ( response.success ){
					// Add success class
					$row.addClass( 'uo-ld-group-drip-row--success' );
					$row.find('input').val('')
					$row.find('select').prop('selectedIndex', 0);
					$row.data('sort',0);
					this.$elements.dataTable.draw(false);
				}
				else {
					// Add error class
					$row.addClass( 'uo-ld-group-drip-row--error' );
				}

				// Remove class after three seconds
				setTimeout(() => {
					// Remove success and error classes
					$row.removeClass( 'uo-ld-group-drip-row--success uo-ld-group-drip-row--error' );
				}, 3000 );
			}, ( response ) => {
				// Add error class
				$row.addClass( 'uo-ld-group-drip-row--error' );

				// Remove class after three seconds
				setTimeout(() => {
					// Remove success and error classes
					$row.removeClass( 'uo-ld-group-drip-row--success uo-ld-group-drip-row--error' );
				}, 3000 );
			});
		});
	}

	addDataTables(){
		// Init DataTables
		this.$elements.table.on( 'init.dt', function () {
			uoAddSwitcher();
		} );

		this.$elements.dataTable = this.$elements.table.DataTable({
			aoColumns: [
				{
					bSortable: true,
					bSearchable: true
				},
				{
					bSortable: true,
					bSearchable: false,
					orderData: [ 2 ],
					targets: [ 2 ]
				},
				{
					bSortable: true,
					bVisible: false,
					sType: 'numeric',
					targets: [ 1 ]
				},
				{
					bSortable: false,
					bVisible: false
				},
				{
					bSortable: false
				}
			],
			order: [
				[ 1, 'asc' ]
			],
			lengthMenu: [
				[ 25, 50, 100, -1 ],
				[ 25, 50, 100, UncannyToolkitPro.i18n.all ]
			]
		});
	}

	hasGroupDripTable(){
		return this.$elements.table.length > 0;
	}
}

export default GroupDrip;